import React, { Component } from 'react';
import {
    Button,
    Card,
    CardHeader,
    CardBody,
    Row,
    Col,
    Container,
    FormGroup,
    Label,
    CardImg
} from "reactstrap";
import _ from "lodash";
import { Link } from "react-router-dom";
import adminApi from '../../../api/adminApi';
import { toast } from 'react-toastify';

import { getUserFullName } from 'helpers/UtilsHelper';
import { FormInput } from 'components/Form';
import ErrorSummary from 'components/ErrorSummary';
import ConfirmDeleteModal from '../../../components/Modal/ConfirmDeleteModal.jsx';

import './UserDetail.scss';
import './Account.scss';
import defaultAvatar from '../../../assets/img/theme/default-avatar.png';

const misMatchPasswordMessage = "Password does not match";

class UserDetail extends Component {
    state = {
        notificationModal: false,
        alert: null,
        userDetail: {},
        firstName: null,
        lastName: null,
        email: '',
        password: '',
        confirmPassword: '',
        errors: {},
        isFormReady: {},
        isDeleteAccount: false,
        avatar: null,
    };

    componentDidMount() {
        this.getDetail(this.props.match.params.id);
    }

    getDetail = (id) => {
        adminApi.getAdminDetail(id).then(res => {
            this.setState(res)
        }).catch(err => {
            console.error(err);
        })
    }

    componentWillReceiveProps(nextProps) {
        if (this.props.match.params.id !== nextProps.match.params.id) {
            this.getDetail(nextProps.match.params.id);
        }
    }

    handleChange = e => {
        const { name, value } = e.target;
        this.setState({
            [name]: value
        },
            () => {
                if (name === 'confirmPassword' || name === "password") {
                    const { password, confirmPassword } = this.state;
                    if (password !== confirmPassword) {
                        this.setState({ errors: { ...this.state.errors, confirmPassword: [misMatchPasswordMessage] } })
                    } else {
                        const tempError = this.state.errors;
                        delete tempError['confirmPassword'];
                        this.setState({ errors: tempError });
                    }
                }
            }
        )
    }

    handleFile = (event) => {
        const { name, files } = event.target;
        if (files.length) {
            this.setState({
                [name]: files[0]
            })
        }
    }

    handleError = (field, status) => {
        let check = true;
        if (status) {
            for (let key in this.state.isFormReady) {
                check = check && this.state.isFormReady[key];
            }
        }
        this.setState(
            { isFormReady: { ...this.state.isFormReady, [field]: status, check: check && status } }
        );
    }

    updateAdminDetail = () => {
        const { firstName, lastName, email, password, confirmPassword, avatar, id } = this.state
        const submitData = { firstName, lastName, email, avatar, id }
        if (_.isString(submitData.avatar) || _.isNull(submitData.avatar)) {
            delete submitData['avatar'];
        }
        if (password) {
            if (password !== confirmPassword) {
                this.setState({ errors: { ...this.state.errors, confirmPassword: [misMatchPasswordMessage] } })
            }
            else {
                const payload = {
                    ...submitData,
                    password
                }
                this.callApi(payload)
            }
        }
        else {
            this.callApi(submitData)
        }
    }

    callApi(payload) {
        adminApi.updateAdminDetail(payload).then(() => {
            toast.success("Update Account success !");
        }).catch(err => {
            const errors = err.response.data;
            this.setState({ errors });
            toast.error("Update Account fail !");
        })
    }

    deleteAdminAccount = () => {
        const id = this.state.id
        adminApi.deleteAdminAccount(id).then(() => {
            toast.success("Delete Account success !");
            this.goToSystemPage();
        }).catch(err => {
            console.error(err);
            toast.error("Delete Account fail !");
        })
    }

    toggleConfirmDelete = () => {
        this.setState({ isDeleteAccount: !this.state.isDeleteAccount });
    }

    goToSystemPage = () => {
        this.props.history.push("/admin/system")
    }

    render() {
        const item = this.state;
        const { errors, isFormReady, firstName, lastName, email, avatar } = this.state
        const formValid = firstName && email && lastName && isFormReady.check;
        const fullName = getUserFullName(item)
        return (
            <>
                <ConfirmDeleteModal
                    className="modal-dialog-centered"
                    isOpen={this.state.isDeleteAccount}
                    toggle={this.toggleConfirmDelete}
                    handle={this.deleteAdminAccount}
                    content={`Do you want to delete "${fullName}" account?`}
                />
                <Container className="mt-6 d-flex justify-content-center align-items-center user-detail" fluid >
                    <Card className="card-profile card-custom">
                        <Row className="align-items-center card-profile-image">
                            <div onClick={() => this.openImageFile.click()}>
                                <CardImg
                                    alt="..."
                                    className="rounded-circle custom-avatar avatar"
                                    src={_.isObject(avatar) ? URL.createObjectURL(avatar) : _.isString(avatar) ? avatar : defaultAvatar}
                                />
                            </div>
                        </Row>
                        <CardHeader className="text-center border-0 pb-0">
                            <Row className="justify-content-center mt-4">
                                <Col>
                                    <input
                                        ref={(ref) => this.openImageFile = ref}
                                        onChange={this.handleFile}
                                        className="d-none"
                                        type="file"
                                        name="avatar"
                                        id="roomImage"
                                        accept=".jpg,.gif,.jpeg,.png" />
                                    <Button
                                        className="mt-4 align-items-center"
                                        color="link"
                                        onClick={() => this.openImageFile.click()}
                                    >
                                        Change Image
                                    </Button>
                                    <div className="error-text mt-3">{errors['image']}</div>
                                </Col>
                            </Row>
                        </CardHeader>
                        <CardBody className="border-0 pb-0 mx-3 wrapper__body">
                            <FormGroup>
                                <Row>
                                    <Label className="form-control-label" >
                                        First Name
                                        <sup className="text-required">*</sup>
                                    </Label>
                                </Row>
                                <Row className="d-block">
                                    <FormInput
                                        type="text"
                                        name="firstName"
                                        handleError={this.handleError}
                                        errors={errors['firstName']}
                                        onChange={this.handleChange}
                                        required={true}
                                        value={item.firstName || ""}
                                    />
                                </Row>
                            </FormGroup>
                            <FormGroup>
                                <Row>
                                    <Label className="form-control-label" >
                                        Last Name
                                        <sup className="text-required">*</sup>
                                    </Label>
                                </Row>
                                <Row className="d-block">
                                    <FormInput
                                        type="text"
                                        name="lastName"
                                        handleError={this.handleError}
                                        errors={errors['lastName']}
                                        onChange={this.handleChange}
                                        required={true}
                                        value={item.lastName || ""}
                                    />
                                </Row>
                            </FormGroup>
                            <FormGroup>
                                <Row>
                                    <Label className="form-control-label" >
                                        E-mail
                                        <sup className="text-required">*</sup>
                                    </Label>
                                </Row>
                                <Row className="d-block">
                                    <FormInput
                                        type="email"
                                        name="email"
                                        handleError={this.handleError}
                                        errors={errors['email']}
                                        onChange={this.handleChange}
                                        regex="email"
                                        required={true}
                                        value={item.email || ""}
                                        disabled={item.email ? true : false}
                                    />
                                </Row>
                            </FormGroup>
                            <FormGroup>
                                <Row>
                                    <Label className="form-control-label" >
                                        Password
                                    </Label>
                                </Row>
                                <Row className="d-block">
                                    <FormInput
                                        type="password"
                                        name="password"
                                        handleError={this.handleError}
                                        errors={errors['password']}
                                        onChange={this.handleChange}
                                        minLength={8}
                                        regex="password"
                                    />
                                </Row>
                            </FormGroup>
                            <FormGroup>
                                <Row>
                                    <Label className="form-control-label" >
                                        Confirm Password
                                    </Label>
                                </Row>
                                <Row className="d-block">
                                    <FormInput
                                        type="password"
                                        name="confirmPassword"
                                        handleError={this.handleError}
                                        errors={errors['confirmPassword']}
                                        onChange={this.handleChange}
                                    />
                                </Row>
                            </FormGroup>
                            <ErrorSummary error={errors} className="mt-3" />
                        </CardBody>
                        <CardBody className="pb-md-4">
                            <Row>
                                <Col className="mb-3 d-flex justify-content-center">
                                    <div className="text-center w-75">
                                        <Button color="danger" onClick={this.toggleConfirmDelete} className="mb-3 w-100">
                                            Remove User
                                        </Button>
                                        <Link to={`/admin/system`} className="d-block w-100 mb-3">
                                            <Button className="w-100" color="link"> CANCEL</Button>
                                        </Link>
                                        <Button color="success" className="w-100" disabled={false && formValid} onClick={this.updateAdminDetail}>
                                            Save Changes
                                        </Button>
                                    </div>
                                </Col>
                            </Row>
                        </CardBody>
                    </Card>
                </Container>
            </>
        );
    }
}

export default UserDetail;