import React from "react";

import {
  Button,
  Modal
} from "reactstrap";


export default function ConfirmDeleteModal(props) {
  const { isOpen, toggle, handle, content, showTitle = true } = props;
  return (
    <Modal className="modal-dialog-centered" isOpen={isOpen} toggle={toggle}>
      <div className="modal-header">
        {showTitle && <h6 className="modal-title" id="modal-title-default"> Confirm Delete!</h6>}
        <button aria-label="Close" className="close" data-dismiss="modal" type="button" onClick={toggle}>
          <span aria-hidden={true}>×</span>
        </button>
      </div>
      <div className="modal-body text-center">
        <b>{content}</b>
      </div>
      <div className="modal-footer">
        <Button color="danger" type="button" onClick={handle}>Yes</Button>
        <Button className="ml-auto" color="secondary" data-dismiss="modal" type="button" onClick={toggle}>Cancel</Button>
      </div>
    </Modal>
  )
}
